const now = new Date();
const howManyDaysToShow = 7;
const previousDays = getPreviousDays(howManyDaysToShow);

console.log();

const labels = previousDays.map(el => el.toLocaleString('fr-FR', {weekday: 'long'}));

function getPreviousDays() {
    const days = [];
    for (let i = 0; i < howManyDaysToShow; i++) {
        days.push(new Date(now.setDate(now.getDate() - 1)));
    }
    return days;
}

const data = {
    labels: labels,
    datasets: [{
        label: 'My First dataset',
        backgroundColor: 'rgb(255, 99, 132)',
        borderColor: 'rgb(255, 99, 132)',
        data: [5, 3, 4, 1, 0, 2],
    }]
};

const config = {
    type: 'line',
    data: data,
    options: {
        scales: {
            y: {
                beginAtZero: true,
                ticks: {
                    stepSize: 1
                }
            }
        }
    }
};

const myChart = new Chart(
    document.getElementById('myChart'),
    config
);